* {
  margin: 0;
  padding: 0;
}

html {
  font-family: 'Pacifico', sans-serif !important;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Pacifico', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/static/media/bg.0cbec15a.jpeg) no-repeat center center fixed;
}

a, a:hover {
  color: black;
}

code {
  font-family: 'Pacifico', monospace;
}
.App {
  text-align: center;
}

.App-header {
  /* background-color: #282c34; */
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
  background: rgba(40,44,52, 0.8)
}
.ingredientImage {
    position: relative;
    padding: 10px;
    width: 50%;
}

.selectFile {
    margin-left: auto;
    margin-right: auto;
