    padding-top: 10px;
}

.recipeLink {
    display: block;
    padding-top: 10px;
}

 .recipeLink {
     display: block;
     padding-top: 10px;
     color: #ffffff;
 }

.selected-ingredient {
    text-align: center;
    width: 100%;
}

.title {
  text-align: center;
  background: rgba(40,44,52,0.001)
}

.thot-image {
  height: 20%;
  width: 20%;
  margin-left: 65%;
}

.title-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #ffffff;
  padding-top: 5px;
  padding-bottom: 10px;
}

.title-image {
  border-radius: 50%;
  width: 50%;
  height: 75%;
  padding-top: 5px;
  padding-bottom: 10px;
}

.title-btn {
  border: #ffffff 4px solid;
  padding: 8px 10px;
  border-radius: 5px;
  color: #ffffff;
}

#h3 {
  font-style: italic;
  padding-top: 200px;
  padding-bottom: 200px;
}


.centerInPage {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.card {
    width: 100%;
    border-style: none;
}

.cardGrid {
    margin: 15px !important;
}
